import React, { useState } from 'react'
import { useWeb3React } from '@web3-react/core'
import { useDispatch } from 'react-redux'

import { CoinbaseWallet, injected, WalletConnect } from '../wallet/Connectors'
import { setAlert } from '../../actions/alert'
import formatAddress from '../../utils/formatAddress'

function ConnectModal() {
  const dispatch = useDispatch()
  const [isModalShow, toggleModal] = useState(false)
  const { activate, active, account } = useWeb3React()

  const connect = async (_connector: any) => {
    await activate(_connector)
    if (account) {
      dispatch(
        setAlert(
          'Wallet connected with ' + formatAddress(account),
          'success'
        ) as any
      )
    }
    toggleModal(false)
  }

  return (
    <div>
      {active ? (
        <button className="border text-dark_text font-medium border-dark_text/20 px-3 py-2 rounded">
          {account ? formatAddress(account) : ''}
        </button>
      ) : (
        <button
          className="border text-dark_text font-medium border-dark_text/20 px-3 py-2 rounded"
          onClick={() => toggleModal(true)}
        >
          Connect
        </button>
      )}

      <div
        className={`fixed top-0 left-0 w-full h-screen bg-black/60 z-20 backdrop-blur justify-center items-center ${
          isModalShow ? 'flex' : 'hidden'
        } `}
      >
        <div className="bg-white/70 backdrop-blur p-10 rounded-md overflow-hidden relative">
          <div className="">
            <p className="text-center font-medium text-3xl">
              Connect your wallet
            </p>
            <p
              className="absolute right-4 top-4 cursor-pointer font-bold"
              onClick={() => {
                toggleModal(false)
              }}
            >
              &#x2715;
            </p>
          </div>
          <div className="mt-5 grid grid-cols-2 gap-5">
            <div
              className="bg-[#EBEBEC] px-4 h-12 rounded-lg flex items-center gap-2 cursor-pointer"
              onClick={() => connect(injected)}
            >
              <img
                src="/img/metamask.svg"
                alt=""
                className="rounded-full"
                width={30}
              />
              <p className="text-dark_text">Metamask</p>
            </div>
            <div
              className="bg-[#EBEBEC] px-4 h-12 rounded-lg flex items-center gap-2 cursor-pointer"
              onClick={() => connect(CoinbaseWallet)}
            >
              <img
                src="/img/coinbase.svg"
                alt=""
                className="rounded-full"
                width={30}
              />
              <p className="text-dark_text">Coinbase</p>
            </div>
            <div
              className="bg-[#EBEBEC] px-4 h-12 rounded-lg flex items-center gap-2 cursor-pointer"
              onClick={() => connect(WalletConnect)}
            >
              <img
                src="/img/walletconnect.svg"
                alt=""
                className="rounded-full"
                width={30}
              />
              <p className="text-dark_text">Wallet Connect</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default ConnectModal
