import React, { ChangeEvent, FormEvent, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Link, Navigate, useNavigate } from 'react-router-dom'
import { Trans } from 'react-i18next'
import { Auth } from 'aws-amplify'

import { RootState, store } from './../../store'
import { setAlert } from '../../actions/alert'

const Register = () => {
  const dispatch = useDispatch()
  const [showModal, setShowModal] = useState(false)
  const [code, setCode] = useState('')
  const [verifyErrorTxt, setVerifyErrorTxt] = useState('')
  const navigate = useNavigate()

  const [formData, setFormData] = useState({
    email: '',
    password: '',
    password2: ''
  })

  const isAuthenticated = useSelector(
    (state: RootState) => state.auth.isAuthenticated
  )

  const { email, password, password2 } = formData

  const onChange = (e: ChangeEvent<HTMLInputElement>) =>
    setFormData({ ...formData, [e.target.name]: e.target.value })

  const onSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault()

    if (password !== password2) {
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      dispatch(setAlert('Passwords do not match', 'danger') as any)
      return false
    }

    await signUp()
  }

  if (isAuthenticated) {
    return <Navigate to="/" />
  }

  async function signUp() {
    try {
      const { user } = await Auth.signUp({
        username: email,
        password,
        attributes: {
          email // optional
          // phone_number // optional - E.164 number convention
          // other custom attributes
        }
      })
      console.log(user)

      setShowModal(true)
      store.dispatch(setAlert('User registered successfully', 'success'))
      // dispatch(register(user) as any)
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
    } catch (error: any) {
      console.log('errr signing up:', error)
      // console.log('errr signing up:', error.message)
      // console.log('errr signing up:', error.code)

      if (error.code === 'UsernameExistsException') {
        store.dispatch(
          setAlert(
            'User already exist, we sent verification code to your email. Please confirm verification code',
            'warning'
          )
        )
        await resendConfirmationCode()
        setShowModal(true)
      } else {
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        dispatch(setAlert(error.message, 'error') as any)
      }
    }
  }

  async function confirmSignUp() {
    try {
      await Auth.confirmSignUp(email, code)
      store.dispatch(setAlert('Email verified successfully', 'success'))
      navigate('/login')
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
    } catch (error: any) {
      console.log('error confirming sign up', error)
      setVerifyErrorTxt(error.message)
    }
  }

  async function resendConfirmationCode() {
    try {
      await Auth.resendSignUp(email)
      console.log('code resent successfully')
      store.dispatch(setAlert('Code resent successfully', 'success'))
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
    } catch (error: any) {
      console.log('error resending code: ', error)
      setVerifyErrorTxt(error.message)
    }
  }

  return (
    <section className="n-container">
      <div className="max-w-[400px] m-auto my-20">
        <h1 className="text-dark_text text-5xl font-bold text-center">
          <Trans i18nKey="sign up" />
        </h1>
        <form className="form text-dark_text" onSubmit={onSubmit}>
          {/* <div className="mt-4">
            <p>Username</p>
            <input
              type="text"
              placeholder="Name"
              name="name"
              className="w-full outline-0 border border-color1 px-4 py-2 border-b-4"
              value={name}
              required
              onChange={onChange}
            />
          </div> */}
          <div className="mt-4">
            <p>Email</p>
            <input
              type="email"
              placeholder="Email Address"
              className="w-full outline-0 border border-color1 px-4 py-2 border-b-4"
              name="email"
              value={email}
              required
              onChange={onChange}
            />
          </div>
          <div className="mt-4">
            <p>Password</p>
            <input
              type="password"
              placeholder="Password"
              name="password"
              className="w-full outline-0 border border-color1 px-4 py-2 border-b-4"
              value={password}
              required
              onChange={onChange}
              min={8}
            />
          </div>
          <div className="mt-4">
            <p>Confirm password</p>
            <input
              type="password"
              placeholder="Confirm Password"
              name="password2"
              className="w-full outline-0 border border-color1 px-4 py-2 border-b-4"
              value={password2}
              required
              onChange={onChange}
              min={8}
            />
          </div>
          <div className="flex justify-center mt-5">
            <button
              type="submit"
              className="px-4 py-2 w-max border border-color1 hover:bg-light_background bg-white m-auto rounded-md"
            >
              <Trans i18nKey="sign up" />
            </button>
          </div>
        </form>
        <p className="my-1 text-center text-dark_text">
          Already have an account? <Link to="/login">Sign In</Link>
        </p>
      </div>

      {showModal ? (
        <>
          <div className="justify-center items-center flex overflow-x-hidden overflow-y-auto fixed inset-0 z-50 outline-none focus:outline-none">
            <div className="relative max-w-[400px]  w-[80%] m-auto my-6 mx-auto">
              {/* content */}
              <div className="border-0 rounded-lg shadow-lg relative flex flex-col bg-white outline-none focus:outline-none">
                {/* header */}
                <div className="p-4">
                  <div className="flex justify-between items-center">
                    <p className="font-medium">Email verification.</p>
                    <p
                      className="font-bold text-2xl mb-3 cursor-pointer"
                      onClick={() => setShowModal(false)}
                    >
                      ×
                    </p>
                  </div>
                  <div>
                    <p>
                      We sent verification code to your email ({email}). <br />
                      Please check your email box and input verification code.
                    </p>
                    <div className="flex justify-between gap-1 mt-5 items-center">
                      <input
                        type={'text'}
                        value={code}
                        onChange={(e: ChangeEvent<HTMLInputElement>) => {
                          setCode(e.target.value)
                        }}
                        className="border border-dark_text bg-white px-4 py-2 text-dark_text outline-0 rounded-sm w-full"
                      />
                      <p
                        className="border border-dark_text px-4 py-2 cursor-pointer"
                        onClick={confirmSignUp}
                      >
                        Confirm
                      </p>
                    </div>
                    <div className="mt-3">
                      <p className="text-sm text-warning">{verifyErrorTxt}</p>
                      <p className="text-right">
                        <span
                          className="cursor-pointer"
                          onClick={resendConfirmationCode}
                        >
                          Resend code
                        </span>
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="opacity-25 fixed inset-0 z-40 bg-black"></div>
        </>
      ) : null}
    </section>
  )
}

