import React, { useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Auth } from 'aws-amplify'

import { RootState, store } from './../store'
import { logout } from '../actions/auth'
import { setAlert } from '../actions/alert'
import { Link } from 'react-router-dom'

function Profile() {
  const dispatch = useDispatch()
  const user = useSelector((state: RootState) => state.auth)
  const [isWithdrawalModal, toggleWithdrawModal] = useState(false)

  async function signOut() {
    try {
      await Auth.signOut({ global: true })
      dispatch(logout())
    } catch (error) {
      console.log('error signing out: ', error)
    }
  }

  async function Withdrawal() {
    try {
      Auth.currentAuthenticatedUser({
        bypassCache: true // Optional, By default is false.
      })
        .then((user) => {
          user.deleteUser((error: any) => {
            if (error) {
              throw error
            }

            store.dispatch(
              setAlert('Account was withdrawed successfully', 'success')
            )
            // Log the user out
            signOut()
          })
        })
        .catch((err) => console.log(err))
    } catch (error) {
      console.log('error signing out: ', error)
    }
  }

  return (
    <div className="n-container">
      <div className="sm:flex justify-between gap-10 py-10">
        <div className="sm:w-1/2 bg-light_background p-10 sm:p-16">
          <div className="flex justify-start gap-2 items-center">
            <img
              src="/img/user.png"
              alt=""
              className="h-32 rounded-full overflow-hidden"
            />
            <div className="font-medium text-dark_text">
              <p className="text-3xl">Unknown</p>
            </div>
          </div>
          <div className="text-lg mt-5 pl-5">
            <p>Email: {user.attributes.email}</p>
          </div>
        </div>
        <div className="sm:w-1/2">
          <Link to={'/edit-profile'}>
            <p className="border border-dark_text/50 py-2 w-full text-center mt-2 cursor-pointer hover:bg-light_background text-dark_text font-medium">
              Edit profile
            </p>
          </Link>
          <p className="border border-dark_text/50 py-2 w-full text-center mt-5 cursor-pointer hover:bg-light_background text-dark_text font-medium">
            Winning prize list
          </p>
          <p className="border border-dark_text/50 py-2 w-full text-center mt-5 cursor-pointer hover:bg-light_background text-dark_text font-medium">
            Bidden Comedy content list
          </p>
          <p
            className="border border-dark_text/50 py-2 w-full text-center mt-5 cursor-pointer hover:bg-light_background text-dark_text font-medium"
            onClick={signOut}
          >
            Logout
          </p>
          <p
            className="border border-dark_text/50 py-2 w-full text-center mt-5 cursor-pointer hover:bg-light_background text-dark_text font-medium"
            onClick={() => toggleWithdrawModal(true)}
          >
            Withdraw
          </p>
        </div>
      </div>

      {isWithdrawalModal ? (
        <>
          <div className="justify-center items-center flex overflow-x-hidden overflow-y-auto fixed inset-0 z-50 outline-none focus:outline-none">
            <div className="relative max-w-[400px]  w-[80%] m-auto my-6 mx-auto">
              {/* content */}
              <div className="border-0 rounded-lg shadow-lg relative flex flex-col bg-white outline-none focus:outline-none">
                {/* header */}
                <div className="p-4">
                  <div className="flex justify-between items-center">
                    <p className="font-medium">Withdraw account.</p>
                    <p
                      className="font-bold text-2xl mb-3 cursor-pointer"
                      onClick={() => toggleWithdrawModal(false)}
                    >
                      ×
                    </p>
                  </div>
                  <div>
                    <p className="bg-warning text-white p-4">
                      Do you want to withdraw account?
                    </p>
                    <p className="mt-3 font-medium ">Reason:</p>
                    <textarea className="border border-dark_text bg-white px-4 py-2 h-32 text-dark_text outline-0 rounded-sm w-full" />
                    <p
                      className="border border-dark_text mt-2 text-center px-4 py-2 cursor-pointer"
                      onClick={() => {
                        Withdrawal()
                        toggleWithdrawModal(false)
                      }}
                    >
                      Withdraw
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="opacity-25 fixed inset-0 z-40 bg-black"></div>
        </>
      ) : null}
    </div>
  )
}

export default Profile
