import React, { useState } from 'react'
import Archive from './../components/section/Archive'
import Auction from './../components/section/Auction'
import CommentReception from './../components/section/CommentReception'
import Ranking from './../components/section/Ranking'
import VotingReception from './../components/section/VotingReception'

const Home = () => {
  const [currentNav, setCurrentNav] = useState('comment-reception')

  const tabs = [
    { title: 'Comment Reception', slug: 'comment-reception' },
    { title: 'Voting Reception', slug: 'voting-reception' },
    { title: 'Auction', slug: 'auction' },
    { title: 'Ranking', slug: 'ranking' },
    { title: 'Archive', slug: 'archive' }
  ]

  return (
    <section className="landing">
      <div className="bg-light_background">
        <div className="n-container">
          <div className="max-w-full overflow-x-auto">
            <div className="flex justify-start max-w-full">
              {tabs.map((row, key) => {
                return (
                  <div
                    key={key}
                    className={`px-4 py-4 flex justify-center text-center items-center cursor-pointer border-b-4 hover:border-color2 ${
                      currentNav === row.slug
                        ? 'border-color2'
                        : 'border-transparent'
                    }`}
                    onClick={() => {
                      setCurrentNav(row.slug)
                    }}
                  >
                    {row.title}
                  </div>
                )
              })}
            </div>
          </div>
        </div>
      </div>

      {currentNav === 'comment-reception' ? <CommentReception /> : ''}
      {currentNav === 'voting-reception' ? <VotingReception /> : ''}
      {currentNav === 'auction' ? <Auction /> : ''}
      {currentNav === 'ranking' ? <Ranking /> : ''}
      {currentNav === 'archive' ? <Archive /> : ''}
    </section>
  )
}

export default Home
